/* progressBar.css */

.progress-bar {
    width: 100%;
    height: 20px;
    background-color: #f0f0f0;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .progress-bar-fill {
    height: 100%;
    background-color: #4caf50; /* Adjust color as needed */
    border-radius: 5px;
  }
  